@import 'components/variables';

.section-alojamientos {
  padding: 5% 0;
  .title-page {
    font-size: 3.5rem;
    font-weight: 900;
    color: $violet;
  }
  .content-detalles {
    display: flex;
    align-items: center;
    .fila1 {
      flex: 0 0 25%;
      display: flex;
      align-items: center;
      p {
        margin-bottom: 0;
        color: $text-gray;
        font-weight: bold;
        font-size: 1.2rem;
      }
    }
    .fila2 {
      flex: 0 0 50%;
      display: flex;
      align-items: center;
      .img-icons {
        width: 15%;
        margin-right: .8rem;
      }
      p {
        margin-bottom: 0;
      }
    }
    .fila3 {
      flex: 0 0 25%;
      display: flex;
      align-items: center;
      .img-icons {
        width: 30%;
        margin-right: .8rem;
      }
      .form-select {
        width: 100px;
        margin: 0 auto;
        border: none;
        padding: .375rem 1.4rem .375rem .75rem;
      }
      .vistas {
        text-align: center;
        .img-icons2 {
          width: 30%;
          cursor: pointer;
        }
      }
    }
  }
  .accordion-button:not(.collapsed) {
    color: $violet;
    font-weight: bold;
    background-color: #f0d5f8;
  }
  .accordion-button {
    font-weight: bold;
    &:focus {
      border-color: #86b7fe;
      box-shadow: none
    }
  }
  .accordion-item:last-of-type {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}
  .clean-filtro {
    padding: 2rem;
    text-align: center;
    font-weight: bold;
    font-size: 1.3rem;
    border: 1px solid rgba(0,0,0,.125);
    border-top: 0;
    border-bottom-right-radius: .25rem;
    border-bottom-left-radius: .25rem;
    a {
      color: $text-gray;
      padding: .4rem;
      border: 2px solid $yellow;
      border-radius: .25rem;
      transition: all .3s;
      &:hover {
        color: $violet;
      }
    }
  }
  .search-more {
    position: relative;
    padding: 1rem;
    background-color: $violet;
    border-radius: 5px;
    .text-big {
      color: $white;
      font-size: 2rem;
      font-weight: 700;
      margin-bottom: 0;
      line-height: 1;
    }
    a {
      color: $white;
      font-size: .8rem;
      transition: all .3s;
      &:hover {
        color: $yellow;
      }
    }
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 10%;
      @media (min-width: 992px) {
        width: 30%;
      }
    }
  }
  .img-destacada {
    position: relative;
    border: 1px solid $gray-light;
    .content {
      @media (min-width: 768px) {
        position: absolute;
        width: 100%;
        height: 100%;
      }
      @media (max-width: 767.98px) {
        height: 40vw;
      }
      .destacado {
        background-color: $orange;
        text-align: center;
        color: $white;
        font-weight: bold;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        font-size: .7rem;
        padding: .4rem;
        position: absolute;
        top: 15px;
        left: 0;
        @media (min-width: 768px) {
          font-size: .9rem;
        }
      }
      .img-ppal {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
      .heart {
        position: absolute;
        top: 20px;
        right: 0;
        width: 10%;
        transform: translateX(-20px);
      }
    }
  }
  .detalles-hotel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border: 1px solid $gray-light;
    .valoracion {
      .valor {
        position: relative;
        background-color: $violet;
        padding: 0.5rem 1rem;
        border-radius: 5px;
        color: white;
        font-size: 1.2rem;
        font-weight: 800;
        margin-right: 1rem;
        &:before {
          position: absolute;
          z-index: -1;
          content: "";
          left: -10px;
          bottom: 0px;
          border-style: solid;
          border-width: 0 10px 10px 10px;
          border-color: transparent transparent $violet transparent;
          transition-duration: .3s;
          transition-property: transform;
        }
      }
      img {
        width: 10px;
        @media (min-width: 375px) {
          width: 15px;
        }
        @media (min-width: 482px) {
          width: 23px;
        }
      }
    }
    .name-hotel {
      font-size: 1.5rem;
      font-weight: bold;
      margin-top: 1rem;
    }
    .ubicacion {
      display: flex;
      align-items: center;
      margin-top: 2rem;
      img {
        width: 5%;
        margin-right: .4rem;
      }
    }
    .ubicacion-vertical {
      display: flex;
      align-items: center;
      margin-top: 1rem;
      font-size: .8rem;
      img {
        width: 5%;
        margin-right: .4rem;
      }
    }
  }
  .price-hotel {
    border: 1px solid $gray-light;
    .price {
      font-size: 1.2rem;
      margin-bottom: 0;
    }
    .denominacion {
      font-weight: bold;
      strong {
        color: $violet;
        font-size: 1.7rem;
        font-weight: 700;
      }
    }
    .descrip-reserva {
      margin-bottom: 0;
    }
    .btn-violet-white {
      font-size: .8rem;
      margin-top: 2rem;
    }
  }
}